import "./Header.sass"
import logo from "../assets/img/logo.jpg";
import React from 'react';


import instagram from './../assets/img/instagram.png';
import facebook from "./../assets/img/facebook.png";
import telegram from "./../assets/img/telegram.png";

export let Header =()=>{
    return<div className="header">
        <div className="header__logo">
            <img src={logo} alt="Logo"/>
        </div>
        <div className="header__info">
            
            <span className="header__info-blure">Информация про </span> 
            <span>COVID-19</span>
        </div>

        <div className="header__contacts">
            
            <span className="header__contact-info">
            Контакт центр МОЗ з питань COVID-19 
            </span>
            <span className="header__contacts-phone">
            +380 97 91 97 048
            </span>
            </div>
        <div className="header__social-networks">
            <a href='https://google.com/'>
            <img src={instagram}/>
            </a>
            <a >
            <img src={facebook}/>
            </a>
            <a href="https://tlgg.ru/it_chats">
            <img src={telegram}></img></a >
        </div>
      </div>
}